import { Vehicles } from "../Moduls/vehicles";

export class vehicleState{
    vehicleType:Vehicles=Vehicles.car;
}

export enum vehicleActionType{
    ChangeType="ChangeType",
}

export interface vehicleAction{
    type:vehicleActionType,
    payload?:any;
}

export function changeType(vehicleType:Vehicles):vehicleAction{
    return{type:vehicleActionType.ChangeType,payload:vehicleType}
}

export function vehicleReducer(currentState:vehicleState,action:vehicleAction):vehicleState{
    const newState={...currentState}
    switch(action.type){
        case vehicleActionType.ChangeType:
            newState.vehicleType=action.payload;
        break;
    }

    return newState;
}