import { AppBar,Box,Divider,IconButton,makeStyles,Menu,MenuItem,Tab,Tabs, Toolbar, Tooltip, Typography } from "@mui/material";
import "./myHeader.css";
import { Vehicles, vehicles } from '../../Moduls/vehicles';
import { SyntheticEvent, useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import HomeIcon from '@mui/icons-material/Home';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import PersonSearchIcon from '@mui/icons-material/PersonSearch';
import { changeType, vehicleState } from '../../redux/vehicleState';
import { store } from "../../redux/store";
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';

function MyHeader(): JSX.Element {
    const navigate=useNavigate();
    const location=useLocation();

    const [value, setValue] = useState('');
    const handleChange = (event: SyntheticEvent, newValue: string) => {
        setValue(newValue);
        switch(newValue){
            case '':
                store.dispatch(changeType(Vehicles.car))
            break;
            case '':
                store.dispatch(changeType(Vehicles.motorcycle));
            break;
            case '':
                store.dispatch(changeType(Vehicles.heavy));
            break;
        }
    };

    const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
    const open = Boolean(anchorEl);
    const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
        setAnchorEl(null);
    };

    const exitPage=(path:any)=>{
        setValue('');
        store.dispatch(changeType(Vehicles.car));
        handleClose();
        navigate(path);
    }
    const goToSearch=()=>{
        store.dispatch(changeType(Vehicles.car));
        handleClose();
        navigate("/search")
    }

    useEffect(()=>{
    },[])

    return (
        <div className="myHeader">
			<AppBar position="static" sx={{bgcolor:'primary.main'}}>
                <Toolbar>
                    <Box display={{xs:'block', sm:'none'}}>
                        <IconButton
                            color="inherit"
                            onClick={handleClick}
                        >
                            <MenuIcon/>
                        </IconButton>
                    </Box>
                    <Menu
                        open={open}
                        anchorEl={anchorEl}
                        onClose={handleClose}
                    >
                        <MenuItem onClick={()=>exitPage("/")}><HomeIcon/> </MenuItem>
                        {(location.pathname.match("/search")==null)&&(<Divider/>)}
                        {(location.pathname.match("/search")==null)&&(<MenuItem onClick={()=>goToSearch()}><SearchIcon/> </MenuItem>)}
                        {(location.pathname.match("/contacts")==null)&&(<Divider/>)}
                        {(location.pathname.match("/contacts")==null)&&(<MenuItem onClick={()=>exitPage("/contacts")}><PersonSearchIcon/> </MenuItem>)}
                    </Menu>

                    <Box display={{xs:'none',sm:'block'}}>
                        <Tooltip title={" "}>
                            <IconButton
                                color="inherit"
                                onClick={()=>exitPage("/")}
                            >
                                <HomeIcon/>
                            </IconButton>
                        </Tooltip>

                        {(location.pathname.match("/search")==null)&&
                        (<Tooltip title={" "}>
                            <IconButton
                                color="inherit"
                                onClick={()=>goToSearch()}
                            >
                                <SearchIcon/>
                            </IconButton>
                        </Tooltip>)}

                        {(location.pathname.match("/contacts")==null)&&
                        (<Tooltip title={" "}>
                            <IconButton
                                color="inherit"
                                onClick={()=>exitPage("/contacts")}
                            >
                                <PersonSearchIcon/>
                            </IconButton>
                        </Tooltip>)}
                    </Box>
                    <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                          
                    </Typography>
                    <Tooltip title={""}>
                        <IconButton
                            color="inherit"
                            onClick={()=>exitPage(-1)}
                        >
                            <ArrowBackIcon/>
                        </IconButton>
                    </Tooltip>
                    
                </Toolbar>
                {(location.pathname.match("/search")!=null)&&
                (<Box>
                    <Tabs 
                        value={value} 
                        onChange={handleChange} 
                        centered
                        textColor="inherit"
                        indicatorColor="secondary"
                    >
                        {vehicles.map((item,index)=><Tab key={index} value={item} label={item} sx={{width:1/3}} />)}
                        
                    </Tabs>
                </Box>)}
                
            </AppBar>
            
        </div>
    );
}

export default MyHeader;
