import { Avatar, Box, Button, IconButton, Link, Typography } from "@mui/material";
import "./myLinks.css";



interface MyLinksProps {
	title:string;
    color:string;
    bgcolor:string;
    link:string;
    size:number;
    icon:JSX.Element;
}

function MyLinks(props: MyLinksProps): JSX.Element {

    const showIcon=(icon:JSX.Element, bgcolor:string)=>{
        return <Avatar 
                    sx={{color:'white',bgcolor}}
                >
                    {icon}
                </Avatar>
        
    }

    return (
        <Box 
            sx={{
                bgcolor:props.bgcolor,
                justifyContent:'center',
                fontSize:20,
                width:150,
                margin:'10px',
                borderRadius:10,
                display:'inline-flex'
            }}
        >
            <Link 
                href={props.link} 
                variant="body2"
                sx={{fontSize:'20px',color:props.color}}
                underline="none"
            >
                {props.title}
            </Link>
            {showIcon(props.icon, props.bgcolor)}
        </Box>
        
    );
}

export default MyLinks;
