import { useState, SyntheticEvent, useEffect } from "react";
import { useForm } from "react-hook-form";
import Car from "../../Moduls/car";
import myAxios from "../../util/myAxios";
import "./searchPage.css";
import SingleCar from "../../myProps/singleCar/singleCar";
import SearchIcon from '@mui/icons-material/Search';
import ClearIcon from '@mui/icons-material/Clear';
import { Box, Button, ButtonGroup, CircularProgress, Snackbar, TextField, Typography } from "@mui/material";
import Motorcycle from '../../Moduls/motorcycle';
import Heavy from "../../Moduls/heavy";
import { Vehicles } from "../../Moduls/vehicles";
import SingleMotorcycle from "../../myProps/singleMotorcycle/singleMotorcycle";
import SingleHeavy from "../../myProps/singleHeavy/singleHeavy";
import { store } from "../../redux/store";
import { changeType, vehicleState } from '../../redux/vehicleState';
import { useLocation } from "react-router-dom";
import VehicleModul from "../../Moduls/vehicleModul";
import { Form } from "react-bootstrap";


function SearchPage(): JSX.Element {
    const {register,handleSubmit,formState:{errors}}=useForm<VehicleModul>()
    const carURL="https://data.gov.il/api/3/action/datastore_search?resource_id=053cea08-09bc-40ec-8f7a-156f0677aff3&q="
    const heavyURL="https://data.gov.il/api/3/action/datastore_search?resource_id=cd3acc5c-03c3-4c89-9c54-d40f93c0d790&q="
    const motorcycleURL="https://data.gov.il/api/3/action/datastore_search?resource_id=bf9df4e2-d90d-4c0a-a400-19e15af8e95f&q="
    const [plateNumber,setPlateNumber]=useState('');
    const [car,setCar]=useState<Car>(new Car());
    const [heavy,setHeavy]=useState<Heavy>(new Heavy());
    const [motorcycle,setMotorcycle]=useState<Motorcycle>(new Motorcycle());
    const [loading,setLoading]=useState(false);
    const [showCar,setShowCar]=useState(false);
    const [showMotorcycle, setShowMotorcycle]=useState(false);
    const [showHeavy, setShowHeavy]=useState(false);
    const [showError, setShowError]=useState(false);
    const [activate,setActivate]=useState(false)


    const handlePlateChange=(sender:SyntheticEvent)=>{
        const value=(sender.target as HTMLInputElement).value;
        setPlateNumber(value);
        setActivate(value.length>=7&&value.length<=8);
    }

    useEffect(()=>{
        if(store.getState().vehicleState.vehicleType===undefined){
            store.dispatch(changeType(Vehicles.car));
        }
        console.log(typeof store.getState().vehicleState.vehicleType)
    },[])

    const hideAll=()=>{
        setShowCar(false);
        setShowHeavy(false);
        setShowMotorcycle(false);
        setShowError(false);
        setActivate(false);
    }
    const retriveData=async ()=>{
        setLoading(true)
        hideAll();
        let url='';
        switch(store.getState().vehicleState.vehicleType){
            case Vehicles.car:
                url=carURL;
            break;
            case Vehicles.motorcycle:
                url=motorcycleURL;
            break;
            case Vehicles.heavy:
                url=heavyURL;
            break;
            
        }
        url=url+plateNumber;
        try{
            const data=await myAxios.get(url)
            .then(response=>{
                if (response.status==200){
                    setLoading(false);
                    if(response.data.result.records[0]!=undefined){
                        switch(store.getState().vehicleState.vehicleType){
                            case Vehicles.car:
                                setCar(response.data.result.records[0]);
                                setShowCar(true);
                            break;
                            case Vehicles.heavy:
                                setHeavy(response.data.result.records[0]);
                                setShowHeavy(true);
                            break;
                            case Vehicles.motorcycle:
                                setMotorcycle(response.data.result.records[0]);
                                setShowMotorcycle(true);
                            break;
                        }
                    }else{
                        setShowError(true)
                    }
                }
            })
            .catch(error=>{
                console.log(error.message);
                setShowError(true)
            });
            
        }catch(error){
            console.log(error);
            setShowError(true)

        }
    }

    const reset=()=>{
        setPlateNumber('');
        setCar(new Car());
        setHeavy(new Heavy());
        setMotorcycle(new Motorcycle());
        hideAll();
    }
    
    return (
        <div className="searchPage">
            <br/>
            <Typography sx={{width:'70%',justifyContent:'center',display:'inline-block'}} variant="h4">          </Typography>
            <br/><br/>
                <TextField
                    label=" "
                    error={!activate&&plateNumber.length!=0}
                    variant="outlined"
                    name="mispar_rechev"
                    value={plateNumber}
                    onChange={handlePlateChange}
                    helperText={(!activate&&plateNumber.length!=0)&&("       7  8 ")}
                />
                <br/><br/>
                <Button 
                    color="primary" 
                    variant="contained"
                    type="submit"
                    onClick={retriveData}
                    disabled={!activate}
                >
                    <SearchIcon/>
                </Button>   
                <Button 
                    color="error" 
                    variant="contained"
                    type="reset"
                    onClick={reset}
                >
                    <ClearIcon/>
                </Button>
                <br/>
            {(loading)&&(<br/>)}
            {(loading)&&(<Box sx={{justifyContent:'center'}}><CircularProgress/></Box>)}
            {(showCar)&&(<SingleCar car={car}></SingleCar>)}
            {(showMotorcycle)&&(<SingleMotorcycle motorcycle={motorcycle}></SingleMotorcycle>)}
            {(showHeavy)&&(<SingleHeavy heavy={heavy}></SingleHeavy>)}
            <br/><br/>
            {(showError)&&
            (<Typography 
                sx={{width:'70%',justifyContent:'center',display:'inline-block'}} 
                variant="body1" 
                color="error"
            >
                  ,                
            </Typography>)}

        </div>
    );
}

export default SearchPage;
